<template>
<div>
    <div class="app">
        <div>
            <div class="firstPage">
                <div><p><span :class="{act:hotTree == 1}" @click="hotTree = 1">水滴还正式版<template v-if="joinstate_1">/已参与</template><template v-else>/未参与</template></span><span :class="{act:hotTree == 2}" @click="hotTree = 2">水滴还小额版<template v-if="joinstate_2">/已参与</template><template v-else>/未参与</template></span></p></div>
            </div>
            <treebox v-show="hotTree == 1" :tree_id="1" @listenJoinState="changeOneState"></treebox>
            <treebox v-show="hotTree == 2" :tree_id="2" @listenJoinState="changeTwoState"></treebox>
        </div>

    </div>

    <tabbar></tabbar>
</div>
</template>

<script>

    import treebox from '@/page/waterd/components/treebox'
    import tabbar from '@/components/tabbar';
    export default {
        name: "Waterd",
        components:{
            tabbar,
            treebox
        },

        data()
        {
            return {
                joinstate_1:false,
                joinstate_2:false,
                hotTree:1, //选中财富树类型
            }
        },
        methods:{
            //修改加入状态
            changeOneState(state)
            {
                this.joinstate_1 = state
            },
            changeTwoState(state)
            {
                this.joinstate_2 = state
            },
            gologin()
            {
                this.$router.push({path:'/login'})
            }
        }
    }
</script>

<style scoped>

    @import "../../static/css/waterd.css";
</style>



