<template>
    <transition
            name="custom-classes-transition"
            enter-active-class="animated fadeIn"
    >
    <div>
        <div class="threePage">
            <div class="cengji ov">
                <p class="fl"><img src="@/static/images/tree/cengji_icon2x.png" style="width: .28rem;height: .24rem;vertical-align: middle;margin-right: .1rem;" alt=""><span style="vertical-align: middle;">层级</span><i style="vertical-align: middle;">{{treeInfo.level}}级</i></p>
                <p class="fr"><img src="@/static/images/tree/zhidu_icon2x.png" style="width: .23rem;height: .24rem;vertical-align: middle;margin-right: .1rem;"  alt=""><span style="vertical-align: middle;">推广制度</span><i style="vertical-align: middle;">一推{{treeInfo.limit}}</i></p>
            </div>
        </div>
        <div v-if="tree_id == 1" class="twoPage">
            <img src="@/static/images/tree/jiagou1.png" alt="">
        </div>
        <div v-else class="twoPage">
            <img src="@/static/images/tree/jiagou2.png" alt="">
        </div>
        <div v-if="name">
            <div class="fourPage">
                <div class="content">
                    <p class="icon"><i></i></p>
                    <p class="frameWork">升级</p>
                    <p class="grade">您当前的级别：{{upgradeInfo.vip_level}}级</p>
                    <p class="grade" style="margin-top: .06rem">{{tolevel}} 级需要支付{{upgradMoney_rmb}}元({{upgradMoney}} USDT)</p>
                    <p class="balance">USDT余额</p>
                    <p class="num">{{upgradeInfo.money}}</p>
                    <p class="online"><span></span></p>
                    <p class="upTo">升级到</p>
                    <div class="ste">
                        <div class="stateBox ov" style="height: .2rem;">
                            <i class="fl sub" @click="subLevel" v-if="upgradeInfo.vip_level > 0 && tolevel >  upgradeInfo.vip_level + 1"></i>
                            <p class="fl pr">
                                <span class="line"></span>
                                <span class="vg1">v{{upgradeInfo.vip_level}}</span>
                                <span class="vg2">v{{tolevel}}</span>
                            </p>
                            <i class="fl add" @click="addLevel" v-if="upgradeInfo.vip_level > 0"></i>
                        </div>
                    </div>
                    <p class="upGrade" v-if="this.treeInfo.join_state"><button  @click="upgradeConfirm">立即升级</button></p>
                    <p class="upGrade" v-else><button @click="joinConfirm">立即加入</button></p>
                </div>
            </div>
            <div class="fivePage">
                <div class="content">
                    <p class="icon"><i></i></p>
                    <p class="frameWork">团队</p>
                    <p class="grade">您的下级会员共计：{{teamData.user_count}}人</p>
                    <div class="text">
                        <p>这里的一层是您的直推，二层是一层的直推，三层是二层的直推，以此类推</p>
                    </div>
                    <ul class="listUl">
                        <li v-for="(item, index) in teamData.users" @click="teamkey == index ? teamkey = 0 :  teamkey = index">
                            <div class="listMember" id="listM"><i></i><a href="javascript:void(0);">{{index}}层会员</a><p>（{{item.length}}人）</p><span></span></div>
                            <div class="conUlList" v-show="teamkey == index">
                                <p class="conTitle"><span>手机号</span><span>等级</span><span>微信二维码</span></p>
                                <div class="bodyList">
                                    <p v-for="(uitem, uindex) in item"><span>{{uitem.username}}</span><span style="margin-left: .5rem">{{uitem.vip_level}}</span><img @click.stop="showQrcode(uitem.wechat_code)" :src="uitem.wechat_code" alt=""></p>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="sixPage">
                <div class="content">
                    <p class="icon"><i></i></p>
                    <p class="frameWork">财务</p>
                    <p class="totalPay ov"><span class="fl tPay">总共支付升级费用</span><span class="fr tText">{{treeFinance.head['expense']}}</span></p>
                    <p class="totalPay ov"><span class="fl tPay">总共收取下级升级费用</span><span class="fr tText">{{treeFinance.head['income']}}</span></p>
                    <div class="record">
                        <p><img src="@/static/images/tree/caiwulius_icon2x.png" alt=""><span>财务流水记录</span></p>
                        <div class="recordList">
                            <div class="recordP" v-for="item in treeFinance.list"><span>{{item.userinfo.nickname}}</span><span>{{item.datetime}}</span><span>{{item.money}}</span></div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <van-dialog
                v-model="codeshow"
                title="用户二维码"
                show-cancel-button
                :showCancelButton="false"
                style="text-align: center"
        >
            <img :src="show_qrcode" style="width: 2rem;height: 2rem;">
        </van-dialog>
    </div>
    </transition>
</template>

<script>
    import {mapGetters} from 'vuex'
    import { toast, Dialog } from 'vant';
    import fmath from "@/utils/math"
    import {treeInfo, upgradeInfo, upgrade, treeTeam, treeWater, joinTree} from "@/api/tree";
    export default {
        name: "treebox",
        props: ['tree_id'],
        computed: {
            ...mapGetters([
                'avatar',
                'name',
                'token'
            ]),
        },
        components:{
            [Dialog.Component.name]: Dialog.Component
        },
        data(){
            return {
                codeshow:false,
                show_qrcode:'',
                treeInfo:{},
                upgradeInfo:{},
                tolevel:0,
                upgradMoney:0,
                upgradMoney_rmb:0,
                joinState:false,
                teamData:{
                    user_count:0,
                    users:[]
                },
                teamkey:0,
                treeFinance:{
                    head:{
                        expense:0,
                        income:0
                    },
                    list:[]
                },
                pageWater:1

            }
        },
        watch:{
            tolevel(val, old)
            {
                this.getUpMoney(val)
            }
        },
        mounted()
        {
            this.getTreeInfo()

        },
        methods:{

            getTreeInit()
            {
                this.pageWater = 1
                this.getUpgradeInfo()
                this.getTreeTeam()
                this.getTreeWater()
            },
            // 获取财富树信息
            getTreeInfo()
            {
                treeInfo({tree_id:this.tree_id}).then(res => {
                    this.treeInfo = res
                    this.$emit('listenJoinState', res.join_state)
                    this.getTreeInit()

                })
            },
            // 获取升级信息
            getUpgradeInfo()
            {
                upgradeInfo({tree_id:this.tree_id}).then(res => {
                    this.upgradeInfo = res
                    this.tolevel = res.vip_level + 1
                })

            },
            // 获取团队成员
            getTreeTeam()
            {
                treeTeam({tree_id:this.tree_id}).then(res => {
                    this.teamData = res
                })
            },

            // 获取财富树流水
            getTreeWater()
            {

                treeWater({tree_id:this.tree_id, page:this.pageWater}).then(res => {
                    this.treeFinance = res
                })

            },
            //加入确认
            joinConfirm()
            {
                Dialog.confirm({
                    title: '系统提醒',
                    message: '加入水滴还，将从您的账户里面扣除'+this.treeInfo.money_rmb+'元('+this.treeInfo.money+'个USDT)，作为一级的支付费用',
                }).then(() => {

                    this.joinTree()
                }).catch(() => {
                    // on cancel
                });
            },
            // 加入财富树
            joinTree()
            {
                joinTree({tree_id:this.tree_id}).then(res => {

                    if (res.error == 1)
                    {
                        Dialog.alert({
                            title: '系统提醒',
                            message: '余额不足, 请立即充值',
                        }).then(() => {

                            this.$router.push('/wallet')
                        });
                        return
                    }
                    Dialog.alert({
                        title: '系统提醒',
                        message: '加入成功!',
                    }).then(() => {

                        this.$emit('listenJoinState', true)
                        this.getTreeInit()
                    });
                })
            },

            // 增加等级
            addLevel()
            {
                if (this.tolevel + 1 <= this.treeInfo.level)
                {
                    this.tolevel++
                }


            },
            // 减少等级
            subLevel()
            {
                if (this.tolevel - 1 > this.upgradeInfo.vip_level)
                {
                    this.tolevel--
                }


            },
            // 获取升级金额
            getUpMoney(tolevel)
            {

                // 计算升级价格
                let upmoney = 0
                let upmoney_rmb = 0
                for (let i = 0; i < tolevel; i++)
                {
                    if (i < this.upgradeInfo.vip_level)
                    {
                        continue
                    }

                    upmoney = fmath.floatAdd(upmoney, this.treeInfo.upgrade_money[i])
                    upmoney_rmb = fmath.floatAdd(upmoney_rmb, this.treeInfo.upgrade_money_rmb[i])
                }

                this.upgradMoney = upmoney
                this.upgradMoney_rmb = upmoney_rmb
            },
            // 升级确认
            upgradeConfirm()
            {
                if (this.upgradeInfo.vip_level == 0)
                {
                    Dialog.alert({
                        title: '系统提醒',
                        message: '请先加入水滴还!',
                    }).then(() => {

                    });
                    return
                }

                Dialog.confirm({
                    title: '系统提醒',
                    message: '升级到'+this.tolevel+'需要支付'+this.upgradMoney_rmb+'元('+this.upgradMoney+'USDT), 确定升级么?',
                }).then(() => {
                    this.upgradeLevel();
                }).catch(() => {
                    // on cancel
                });


            },
            // 升级
            upgradeLevel()
            {
                upgrade({tree_id:this.tree_id, tolevel:this.tolevel}).then(res => {

                    this.upgradeInfo.vip_level = this.tolevel
                    this.tolevel = this.tolevel + 1 <= 9 ? this.tolevel + 1 : 9
                    this.upgradeInfo.money = res.money
                    Dialog.alert({
                        title: '系统提醒',
                        message: '升级成功',
                    }).then(() => {
                    });
                })
            },
            // 显示二维码
            showQrcode(qrcode)
            {
                this.codeshow = true
                this.show_qrcode = qrcode
            }

        }
    }
</script>

